
import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);


import InvenstmentManager from "./components/listers/InvenstmentCards"
import InvenstmentDetail from "./components/listers/InvenstmentDetail"

import CommunityManager from "./components/listers/CommunityCards"
import CommunityDetail from "./components/listers/CommunityDetail"

import Login from "./Login.vue"
import Main from "./components/Main.vue"
import Sub from "./Sub.vue"
import Result from "./Result.vue"
import Done from "./Done.vue"


export default new Router({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes: [
            {
                path: '/invenstments',
                name: 'InvenstmentManager',
                component: InvenstmentManager
            },
            {
                path: '/invenstments/:id',
                name: 'InvenstmentDetail',
                component: InvenstmentDetail
            },

            {
                path: '/communities',
                name: 'CommunityManager',
                component: CommunityManager
            },
            {
                path: '/communities/:id',
                name: 'CommunityDetail',
                component: CommunityDetail
            },
            {
                path: '/',
                name: 'Login',
                component: Login
            },
            {
                path: '/main',
                name: 'Main',
                component: Main
            },
            {
                path: '/sub',
                name: 'Sub',
                component: Sub
            },
            {
                path: '/result',
                name: 'Result',
                component: Result
            },
            {
                path: '/done',
                name: 'Done',
                component: Done
            },
    ]
})
